import './_components';


  document.addEventListener("click", documentActions);

  function documentActions(e) {
    const targetElement = e.target;


    if (targetElement.classList.contains('menu__link--btn')) {

      targetElement.closest('.menu__item').classList.toggle('active');
    }
    if (!targetElement.closest('.menu__item') && document.querySelectorAll('.menu__item.active').length > 0) {
      _removeClasses(document.querySelectorAll('.menu__item.active'), "active");
    }
  }




//RemoveClasses
function _removeClasses(el, class_name) {
  for (var i = 0; i < el.length; i++) {
    el[i].classList.remove(class_name);
  }
}


function slider(activeSlide = 0) {

  const slides = document.querySelectorAll('.item-products')

  slides[activeSlide].classList.add('active')

  slides.forEach(slide => {
    slide.addEventListener('click', () => {
      removeClass()
      slide.classList.add('active')
    })
  })

  function removeClass() {
    slides.forEach(slide => slide.classList.remove('active'))
  }

}

slider()



  const steps = document.querySelectorAll('.howitworks__item');

  if (steps.length > 0) {
    const observerOptions = {
      root: null, // Используем окно просмотра как корневой элемент
      rootMargin: '0px',
      threshold: 0.5 // Элемент считается видимым, если 10% его площади находится в зоне просмотра
    };

    const observerCallback = (entries, observer) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          // Начинаем выполнять ваш код, когда первый элемент становится видимым
          steps[0].classList.add('active');

          let i = 1;

          const intervalId = setInterval(() => {
            if (i < steps.length) {
              steps[i].classList.add('active');
              i++;
            } else {
              clearInterval(intervalId);
            }
          }, 700);

          // Останавливаем наблюдатель после начала анимации
          observer.unobserve(entry.target);
        }
      });
    };

    const observer = new IntersectionObserver(observerCallback, observerOptions);

    // Наблюдаем за первым элементом
    observer.observe(steps[0]);
  }



